import React, { memo } from "react";
import { Typography } from "@mui/material";
import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { decodedUrl, getHostnameFromUrl } from "../../../../utils/helpers";
import HighlightedText from "../../../component/HighlightedText";
import { siteStore } from "../../../../core/stores/siteStore";

const TabUrl = ({ url, searchText, WebkitLineClamp = 2 }) => {
  const { theme, fontWeight, fontColor } = useSettingsContext();

  const tabSiteConfig = siteStore(state => (state.sites.find((d) => d.hostname === getHostnameFromUrl(url))))


  const typographyStyles = {
    whiteSpace: "pre-wrap",
    wordWrap: "break-word",
    wordBreak: "break-all",
    textWrap: "break-word",
    overflow: "hidden",
    textOverflow: "ellipsis",
    display: "-webkit-box",
    WebkitLineClamp: WebkitLineClamp,
    WebkitBoxOrient: "vertical",
    fontWeight: fontWeight - 200 <= 0 ? 100 : fontWeight - 200,
    color: tabSiteConfig?.fontColor ?? (theme === "dark" ? "text.primary" : fontColor || "text.primary"),
    opacity: 0.3,
  };

  return (
    <Typography
      variant="h5"
      color="text.secondary"
      sx={typographyStyles}
    >
      <HighlightedText text={decodedUrl(url)} searchText={searchText} />
    </Typography>
  );
};

export default memo(TabUrl);
