import React, { useEffect, useRef } from "react";
import './App.css';
import { CssBaseline } from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import PageLoading from "./component/pageLoading";
import { userStore } from "../core/stores/userStore";
import { themeOptions } from "./themeOptions";
import { siteStore } from "../core/stores/siteStore";
// userStore.getState().initUserStore();
siteStore.getState().loadSiteConfigs();



export default function App({ children }) {
  console.log("render App @" + Date.now());

  const {
    initUserStore,
    userStoreInitialized,
    theme,
    fontScale,
    fontFamily,
    rtl,
    disableAnimations,
  } = userStore((state) => ({
    initUserStore: state.initUserStore,
    userStoreInitialized: state.userStoreInitialized,
    theme: state.settings.theme,
    fontScale: state.settings.fontScale,
    fontFamily: state.settings.fontFamily,
    rtl: state.settings.rtl,
    disableAnimations: state.settings.disableAnimations,
  }));

  useEffect(() => {
    // initUserStore();
  }, []);


  const documentRef = useRef(document);

  useEffect(() => {
    documentRef.current.documentElement.setAttribute("dir", rtl ? 'rtl' : "ltr");
  }, [rtl]);



  const themeMemo = (() => {
    switch (theme) {
      case "light":
      default:
        return createTheme(themeOptions("light", fontScale, fontFamily, disableAnimations));
      case "dark":
        return createTheme(themeOptions("dark", fontScale, fontFamily, disableAnimations));
    }
  })();

  if (!userStoreInitialized) {
    return <PageLoading />;
  }

  return (
    <ThemeProvider theme={themeMemo}>
      <CssBaseline />
      <ToastContainer />
      {children}
    </ThemeProvider>
  );
}
