import { immer } from "zustand/middleware/immer";
import { getUserConfig, setLocalPassword, setUserConfig, userPublicLocalStorage } from "../user";
import { createWithEqualityFn } from "zustand/traditional";
import { shallow } from "zustand/shallow";
import { appearanceSettings, beautifySettings, quickAccessSettings, historySettings, otherSettings, performanceSettings, pictureInPictureSettings, progressBarSettings, tabGroupsSettings, tabsSettings, bookmarkSettings } from "../../configs/app";

const initializeSettings = {
    ...appearanceSettings,
    ...tabsSettings,
    ...beautifySettings,
    ...historySettings,
    ...pictureInPictureSettings,
    ...tabGroupsSettings,
    ...progressBarSettings,
    ...otherSettings,
    ...performanceSettings,
    ...quickAccessSettings,
    ...bookmarkSettings
};

export const userStoreCacheInstance = async () => {
    return await userPublicLocalStorage('settings');
};

export const userStore = createWithEqualityFn(immer((set) => ({
    userStoreInitialized: false,
    userInfo: {},
    userSession: {},
    searchText: "",
    searchQuery: "",
    settings: initializeSettings,
    initUserStore: async () => {
        const startTime = Date.now();
        console.log('initUserStore start:', startTime, 'ms');
        const settings = await getUserConfig('user_settings');
        if (settings?.backgroundImageUrl) {
            //sync history 20240824 ,after deleted!
            const cacheInstance = await userStoreCacheInstance();
            await cacheInstance.set('backgroundImageUrl', settings.backgroundImageUrl);
            await setUserConfig('user_settings', { ...settings, backgroundImageUrl: "" });
        }
        const cacheInstance = await userStoreCacheInstance();
        const backgroundImageUrl = await cacheInstance.get("backgroundImageUrl");
        set((state) => ({ settings: { ...state.settings, ...settings, backgroundImageUrl }, userStoreInitialized: true }));
        const endTime = Date.now();
        console.log('initUserStore end:', endTime, 'ms');
        console.log('initUserStore duration:', endTime - startTime, 'ms');
    },
    initSettings: async () => {
        const settings = await getUserConfig('user_settings');
        set((state) => ({ settings: { ...state.settings, ...settings } }));
        console.log("User store settings initialized");
    },
    savePassword: async (password) => {
        await setLocalPassword(password);
        const storedSettings = await getUserConfig('user_settings') || {};
        await setUserConfig('user_settings', { ...storedSettings, passwordSet: true, screenLock: false });
        set((state) => ({ settings: { ...state.settings, passwordSet: true, screenLock: false } }));
    },
    setSearchText: (searchText) => {
        set({ searchText });
    },
    setSearchQuery: (searchQuery) => {
        set({ searchQuery });
    },
    updateSetting: async (key, val) => {
        set((state) => ({ settings: { ...state.settings, [key]: val } }));
        if (key === 'backgroundImageUrl') {
            const cacheInstance = await userStoreCacheInstance();
            await cacheInstance.set('backgroundImageUrl', val);
            return;
        }
        const storedSettings = await getUserConfig('user_settings') || {};
        await setUserConfig('user_settings', { ...storedSettings, [key]: val });
    },
    setUser: async (userInfo, userSession) => {
        set({ userInfo, userSession });
    },
    logout: async () => {
        set({ userInfo: {}, userSession: {} });
    }
})), shallow);

export default userStore;