import { Box } from "@mui/material";
import React, { memo } from "react";
import userStore from "../../core/stores/userStore";

const BgOverlay = memo(() => {

    const { theme, colorfulBackground, background, backgroundImageUrl, backgroundImageFade, backgroundImageBlur, backgroundImageBrightness } = userStore((state) => ({

        theme: state.settings.theme,
        colorfulBackground: state.settings.colorfulBackground,
        background: state.settings.background,
        backgroundImageUrl: state.settings.backgroundImageUrl,
        backgroundImageFade: state.settings.backgroundImageFade,
        backgroundImageBlur: state.settings.backgroundImageBlur,
        backgroundImageBrightness: state.settings.backgroundImageBrightness
    }));

    if (!colorfulBackground) {
        return null;
    }

    return (
        <Box sx={{
            position: "fixed",
            zIndex: -1,
            width: "100%",
            height: "100%",
            minHeight: "100vh",
            overflow: "hidden",
            // opacity: 1,
            transition: "transform .4s,opacity .4s",
            background: theme === 'light' && !backgroundImageUrl ? background : ""
        }}>
            <Box sx={{
                transform: "scale(1.1)",
                position: "absolute",
                width: "100%",
                height: "100%",
                backgroundSize: "cover",
                backgroundPosition: "center center",
                transition: "opacity 1s cubic-bezier(.215,.61,.355,1)",
                zIndex: 1,
                opacity: 1,
                // backgroundImage: `-webkit-cross-fade(url(data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==),url(${backgroundImageUrl}),${backgroundImageFade})`,
                backgroundImage: `url(${backgroundImageUrl})`,
                filter: backgroundImageUrl ? `blur(${backgroundImageBlur}px) brightness(${backgroundImageBrightness}) opacity(${theme === 'light' ? backgroundImageFade : backgroundImageFade * 0.75})` : "none"
            }}>

            </Box>
        </Box >
    )
});


export default BgOverlay;