// New Component: HighlightedText.jsx
import React from "react";
import { Typography, Box } from "@mui/material";

const HighlightedText = ({ text, searchText }) => {
    if (!searchText) return <Typography>{text}</Typography>;

    // Process search text, remove extra spaces and split into an array of words
    const searchWords = searchText.trim().replace(/\s+/g, " ").split(" ");

    // Create a case-insensitive regular expression
    const regex = new RegExp(`(${searchWords.join("|")})`, "gi");

    console.log('HighlightedText', text, searchText, text.split(regex))

    return (
        <Typography component="span">
            {Array.from(text.split(regex)).map((part, index) => (
                regex.test(part) ? (
                    <Box key={index} component="span" sx={{ backgroundColor: "#FFFF00", color: "#333" }}>
                        {part}
                    </Box>
                ) : (
                    <Box key={index} component="span">
                        {part}
                    </Box>
                )
            ))}
        </Typography>
    );
};

export default HighlightedText;