import { availableColors, getBackgroundByColor, noColor } from "../../utils/colors";
import { hexToRgb } from "../../utils/helpers";
import userStore from "../../core/stores/userStore";
import { crc32 } from "../../utils/crypto";

export function getTabItemStyle(tabId, discarded = false, color = null) {
    const {
        theme,
        colorfulBackground,
        listBottomBorder,
        listBackgroundAlpha,
        listBackgroundStyle,
        listVerticalSpacing,
        view,
        fontScale,
        fontColor
    } = userStore.getState().settings;

    const paddingY = Math.floor((view === 'visual' ? 6 : 4) * fontScale);

    const getColor = availableColors[isNaN(tabId) ? crc32(tabId) % availableColors.length : tabId % availableColors.length] || noColor;
    const [style, deg] = [...listBackgroundStyle.split("|")]
    const background = getBackgroundByColor(getColor, style, deg, listBackgroundAlpha);

    return {
        width: "100%",
        display: "flex",
        paddingTop: `${listVerticalSpacing}px`,
        paddingBottom: `${listVerticalSpacing}px`,
        flexDirection: "column",
        borderBottomStyle: listBottomBorder,
        borderBottomWidth: "1px",
        borderBottomColor: hexToRgb(getColor.hex, 0.5),
        background: background,
        mixBlendMode: theme === "dark" ? 'difference' : 'normal',
        color: fontColor || 'text.primary',
        opacity: discarded ? 0.6 : 1,
        "&:hover": {
            background: colorfulBackground ? hexToRgb(getColor.hex, theme === "dark" ? 0.25 : 0.25) : (theme === "dark" ? "#666" : "#f5f5f5"),
            // color: "#fff",
            '& .close-button': {
                display: "flex",
            },
        },
        "&.Mui-selected": {
            background: hexToRgb(getColor.hex, theme === "dark" ? 0.3 : 0.25),
            // boxShadow: `0px 5px 10px 0 rgba(0,0,0,0.2)`,
            zIndex: 1,
            color: fontColor || 'text.primary',
            "&:hover": {
                background: hexToRgb(getColor.hex, theme === "dark" ? 0.25 : 0.15)
            },
        }
    };
};