import localCache from "../utils/localCache";
import chromeStorage from "../utils/chromeStorage";
import { decryptDataWithPassword, encryptDataWithPassword, sha256 } from "../utils/crypto";
import { memoryStore } from "../utils/memoryStore";
import { isString } from "lodash-es";

const chromeStorageLocal = chromeStorage("local");

/**
 * Sets the local password for the user.
 *
 * @param {string} password - The password to be set.
 * @return {Promise<void>} - A promise that resolves when the password is set.
 */
export const setLocalPassword = async (password) => {
    const hashedPassword = await sha256(password);
    return chromeStorageLocal.set('user-local-password', hashedPassword);
}

export const setUserConfig = async (key, config) => {
    console.log("setUserConfig", key, config);
    memoryStore.set(key, config);
    return chromeStorageLocal.set(key, config);
}

export const getUserConfig = async (key) => {
    let config = memoryStore.get(key);
    if (config) return config;
    config = await chromeStorageLocal.get(key);
    if (config) memoryStore.set(key, config);
    return config;
}
export const deleteUserConfig = async (key) => {
    memoryStore.delete(key);
    await chromeStorageLocal.remove(key);
    return true;
}

/**
 * Checks if the provided password matches the locally stored password.
 *
 * @param {string} password - The password to be checked.
 * @return {Promise<boolean>} A promise that resolves to true if the password matches, false otherwise.
 */
export const checkLocalPassword = async (password) => {
    const localPassword = await chromeStorageLocal.get('user-local-password');
    if (localPassword) {
        const hashedPassword = await sha256(password);
        return hashedPassword === localPassword;
    }
    return false;
}

export const encryptData = async (data) => {
    const localPassword = await chromeStorageLocal.get('user-local-password');
    const rawStr = isString(data) ? data : JSON.stringify(data);
    return encryptDataWithPassword(localPassword, rawStr);
}

export const decryptData = async (encryptedString) => {
    if (!encryptedString) return null;
    const localPassword = await chromeStorageLocal.get('user-local-password');
    const decryptedStr = await decryptDataWithPassword(localPassword, encryptedString);
    return JSON.parse(decryptedStr);
}

let uuidPromise = null;

export const getCurrentUserUUID = async () => {
    if (uuidPromise) return uuidPromise;

    uuidPromise = (async () => {
        let uuid = memoryStore.get('rabbitpair_active_user_uuid');
        if (uuid) return uuid;

        uuid = await chromeStorageLocal.get('rabbitpair_active_user_uuid');
        if (uuid) {
            memoryStore.set('rabbitpair_active_user_uuid', uuid);
            return uuid;
        }

        uuid = crypto.randomUUID();
        await chromeStorageLocal.set('rabbitpair_active_user_uuid', uuid);
        memoryStore.set('rabbitpair_active_user_uuid', uuid);
        return uuid;
    })();

    return uuidPromise;
};

export const getAccessToken = async () => {

    return crypto.randomUUID();

}

/*eslint no-return-await: "error"*/
export const userPublicLocalStorage = async (namespace = 'stored') => {
    const cacheKey = 'userPublicLocalStorage' + namespace;
    let store = memoryStore.get(cacheKey);
    if (store) return store;

    const userUUID = await getCurrentUserUUID();
    store = localCache('users/' + userUUID + '/public/' + namespace);
    memoryStore.set(cacheKey, store);
    return store;
}

export const userEncryptLocalStorage = async () => {
    const userUUID = await getCurrentUserUUID();
    return localCache('users/' + userUUID + '/encrypted');
}

export const generateSessionKey = async (sessionId, namespace = 'stored') => {
    const userUUID = await getCurrentUserUUID();
    return `users/${userUUID}/${namespace}/${sessionId}`;
}

export const saveEncryptData = async (key, data) => {
    const localCacheEncryptInstance = await userEncryptLocalStorage();
    const encryptedData = await encryptData(data);
    await localCacheEncryptInstance.set(key, encryptedData);
    memoryStore.set(key, data);
}

export const getEncryptData = async (key) => {
    let data = memoryStore.get(key);
    if (data) return data;

    const localCacheEncryptInstance = await userEncryptLocalStorage();
    const encryptedData = await localCacheEncryptInstance.get(key);
    data = await decryptData(encryptedData);
    memoryStore.set(key, data);
    return data;
}
