import { VolumeOff, VolumeUp } from "@mui/icons-material";
import { IconButton, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


const TabVolume = ({ tabId, muted }) => {
  const { t } = useTranslationContext();


  const handleSwitchVolumeTab = async (event) => {
    event.preventDefault();
    event.stopPropagation();
    await chrome.tabs.update(tabId, { muted: !muted });
  };


  return (
    <Tooltip arrow placement="top" title={<Typography>{t(muted ? 'tabMenu.UnmuteTab' : 'tabMenu.MuteTab')}</Typography>}>
      <IconButton sx={{ padding: "4px", color: "inherit" }} onClick={handleSwitchVolumeTab}>
        {muted ? (
          <VolumeOff size="small" />
        ) : (
          <VolumeUp size="small" />
        )}
      </IconButton>
    </Tooltip>
  );
};

export default memo(TabVolume);
