import { openDB } from 'idb';

class LocalCache {
    static stores = new Map();
    static DEFAULT_STORE_NAME = "rabbitpair";
    static DEFAULT_CLEANUP_INTERVAL = 60 * 60 * 1000; // 60 minutes

    constructor(storeName = "", options = {}) {
        this._db = null;
        this._cleanupInterval = null;
        this.storeName = storeName || LocalCache.DEFAULT_STORE_NAME;
        this.options = {
            logErrors: true,
            cleanupInterval: LocalCache.DEFAULT_CLEANUP_INTERVAL,
            ...options
        };
    }

    static async create(storeName = "", options = {}) {
        const instance = new LocalCache(storeName, options);
        await instance.init();
        return instance;
    }

    async init() {
        if (!LocalCache.stores.has(this.storeName)) {
            LocalCache.stores.set(this.storeName, await this.openDatabase());
        }
        this._db = LocalCache.stores.get(this.storeName);
    }

    async openDatabase() {
        const storeName = this.storeName;
        return openDB(storeName, 2, {
            upgrade(db) {
                if (!db.objectStoreNames.contains(storeName)) {
                    db.createObjectStore(storeName);
                }
            }
        });
    }

    async _ensureDbConnection() {
        if (!this._db) {
            await this.init();
        }
    }

    async set(key, items, timeout = 0) {
        const value = {
            key,
            data: items,
            expires: timeout ? Date.now() + timeout * 1000 : 0
        };
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            await db.put(this.storeName, value, key);
            return items;
        } catch (error) {
            this.logError(`Error setting value for key ${key}:`, error);
            throw error;
        }
    }

    async get(key) {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            const value = await db.get(this.storeName, key);
            if (value && (value.expires === 0 || value.expires > Date.now())) {
                return value.data;
            }
            return null;
        } catch (error) {
            this.logError(`Error getting value for key ${key}:`, error);
            return null;
        }
    }

    async getOrSet(key, itemsPromise, timeout = 0) {
        let cacheItem = await this.get(key);
        if (cacheItem) return cacheItem;
        const items = typeof itemsPromise === 'function' ? await itemsPromise() : itemsPromise;
        return await this.set(key, items, timeout);
    }

    async remove(key) {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            await db.delete(this.storeName, key);
        } catch (error) {
            this.logError(`Error removing key ${key}:`, error);
            throw error;
        }
    }

    async delete(key) {
        return this.remove(key);
    }

    async prune() {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            await db.clear(this.storeName);
        } catch (error) {
            this.logError('Error pruning database:', error);
            throw error;
        }
    }

    async clear() {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            await db.clear(this.storeName);
        } catch (error) {
            this.logError('Error clear database:', error);
            throw error;
        }
    }

    async keys(match = "") {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            const keys = await db.getAllKeys(this.storeName);
            return match ? keys.filter(key => key.includes(match)) : keys;
        } catch (error) {
            this.logError('Error getting keys:', error);
            return [];
        }
    }

    async length() {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            return await db.count(this.storeName);
        } catch (error) {
            this.logError('Error getting length:', error);
            return 0;
        }
    }

    async whereIn(keys) {
        try {
            await this._ensureDbConnection();
            return (await Promise.all(keys.map(key => this.get(key)))).filter(Boolean);
        } catch (error) {
            this.logError('Error in whereIn:', error);
            return [];
        }
    }

    async all() {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            const allItems = await db.getAll(this.storeName);
            const now = Date.now();
            return allItems
                .filter(item => item.expires === 0 || item.expires > now)
                .map(item => item.data);
        } catch (error) {
            this.logError('Error getting all items:', error);
            return [];
        }
    }

    // New method: Batch set operation
    async batchSet(items, timeout = 0) {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            const tx = db.transaction(this.storeName, 'readwrite');
            const store = tx.objectStore(this.storeName);
            const expires = timeout ? Date.now() + timeout * 1000 : 0;

            for (const [key, data] of Object.entries(items)) {
                await store.put({ key, data, expires }, key);
            }

            await tx.done;
            return items;
        } catch (error) {
            this.logError('Error in batch set operation:', error);
            throw error;
        }
    }

    // New method: Batch get operation
    async batchGet(keys) {
        try {
            await this._ensureDbConnection();
            const db = await this._db;
            const tx = db.transaction(this.storeName, 'readonly');
            const store = tx.objectStore(this.storeName);
            const now = Date.now();

            const results = await Promise.all(keys.map(async key => {
                const value = await store.get(key);
                if (value && (value.expires === 0 || value.expires > now)) {
                    return [key, value.data];
                }
                return [key, null];
            }));

            await tx.done;
            return Object.fromEntries(results);
        } catch (error) {
            this.logError('Error in batch get operation:', error);
            return {};
        }
    }

    logError(...args) {
        if (this.options.logErrors) {
            console.error(...args);
        }
    }
}

const LocalCacheInstances = new Map();

export default async function getLocalCache(name, options = {}) {
    if (!LocalCacheInstances.has(name)) {
        try {
            const instance = await LocalCache.create(name, options);
            LocalCacheInstances.set(name, instance);
        } catch (error) {
            console.error(`Error creating LocalCache instance for ${name}:`, error);
            throw error;
        }
    }
    return LocalCacheInstances.get(name);
}