export const memoryStore = {
    _store: {},

    // 设置一个键值对到存储中
    set(key, value) {
        this._store[key] = value;
    },

    // 根据键获取存储中的值
    get(key) {
        return this._store[key];
    },

    // 根据键从存储中移除一个值
    remove(key) {
        delete this._store[key];
    },
    // 根据键从存储中移除一个值
    delete(key) {
        delete this._store[key];
    },

    // 清空整个存储
    clear() {
        this._store = {};
    },

    // 获取当前存储中所有的键
    keys() {
        return Object.keys(this._store);
    }
};