import {
  formatTabData
} from "../../core/browers/tabs";
import tabStore from "../../core/stores/tabStore";
import { scrollToActiveTab } from "../../utils/helpers";

const { getTabConfig, setTabConfig, activeTab, closeTab, resortTabs } = tabStore.getState();

const handleTabOnCreated = async tab => {
  const tabId = tab.id;
  const tabData = await formatTabData(tab);
  const tabConfigs = { created_time: Date.now(), lastAccessed: Date.now(), ...tabData };
  await setTabConfig(tabId, tabConfigs);
};

const handleTabOnUpdated = async (tabId, changeInfo, tab) => {
  console.log("handleTabOnUpdated", { tabId, changeInfo, tab });
  const tabConfig = await getTabConfig(tabId);
  const tabData = await formatTabData(tab);
  const tabConfigs = { ...tabConfig, ...tabData };
  await setTabConfig(tabId, tabConfigs);
};

const handleTabOnActivated = async activeInfo => {
  await activeTab(activeInfo.tabId);
  scrollToActiveTab();
};

const handleTabOnRemoved = async (tabId, removeInfo) => {
  console.log("handleTabOnRemoved", { tabId, removeInfo });
  await closeTab(tabId);
};

const handleTabOnReplaced = async (addedTabId, removedTabId) => {
  console.log("handleTabOnReplaced", { addedTabId, removedTabId });
  await closeTab(removedTabId);
  const tab = await chrome.tabs.get(addedTabId);
  const tabConfig = await getTabConfig(addedTabId);
  const tabData = await formatTabData(tab);
  const tabConfigs = { created_time: Date.now(), lastAccessed: Date.now(), ...tabConfig, ...tabData };
  await setTabConfig(addedTabId, tabConfigs);
};

const handleTabOnMoved = async (tabId, moveInfo) => {
  console.log("handleTabOnMoved", { tabId, moveInfo });
  await resortTabs();
};

const handleTabOnDetached = async (tabId, detachInfo) => {
  console.log("handleTabOnDetached", { tabId, detachInfo });
  await resortTabs();
};

const handleTabOnAttached = async (tabId, attachInfo) => {
  console.log("handleTabOnAttached", { tabId, attachInfo });
  await resortTabs();
};


export const sidebarTabChangedEvents = () => {
  chrome.tabs.onCreated.addListener(handleTabOnCreated);
  chrome.tabs.onUpdated.addListener(handleTabOnUpdated);
  chrome.tabs.onActivated.addListener(handleTabOnActivated);
  chrome.tabs.onRemoved.addListener(handleTabOnRemoved);
  chrome.tabs.onReplaced.addListener(handleTabOnReplaced);
  chrome.tabs.onMoved.addListener(handleTabOnMoved);
  chrome.tabs.onAttached.addListener(handleTabOnAttached);
  chrome.tabs.onDetached.addListener(handleTabOnDetached);
};
