import { immer } from "zustand/middleware/immer";
import { userPublicLocalStorage } from "../user";
import { formatTabData } from "../browers/tabs";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';
import { debounce, pick, sortBy } from "lodash-es";
import { faviconURL, fetchWebsiteIcon } from "../../utils/helpers";

export const localCacheMetadataInstance = async () => {
    return await userPublicLocalStorage('metadata')
};
export const localCacheQuickAccessInstance = async () => {
    return await userPublicLocalStorage('quickAccess');
};

export const tabStore = createWithEqualityFn(subscribeWithSelector(immer((set, get) => ({
    currentWindow: {},
    tabs: [],
    groups: [],
    hotTabs: [],
    tabsScrollProgress: {},
    quickAccess: [],
    activeTabId: null,
    activeWindowId: null,
    isActiveTabIntersecting: true,
    tabStoreInitialized: false,
    groupExpanded: true,
    keyItems: [],
    keyboardSelected: 0,
    pressedKeys: {},

    initTabs: async () => {
        if (!get().tabStoreInitialized) {
            const startTime = Date.now();
            get().loadTabs();
            get().loadGroups();
            const endTime = Date.now();
            console.log(`initTabs execution time: ${endTime - startTime}ms`);
        }
    },

    loadTabs: async () => {
        const startTime = Date.now();

        chrome.windows.getAll({ populate: true, windowTypes: ["normal"] }, (windows) => {
            let endTime = Date.now();
            console.log(`loadTabs windows execution time1: ${endTime - startTime}ms`);

            chrome.windows.getCurrent((currentWindow) => {
                endTime = Date.now();
                console.log(`loadTabs currentWindow execution time1: ${endTime - startTime}ms`);

                const openTabs = windows.flatMap(window => window.tabs);

                endTime = Date.now();
                console.log(`loadTabs flatMap openTabs execution time1: ${endTime - startTime}ms`);

                const activeTabId = currentWindow.tabs?.find(tab => tab.active)?.id;

                const updatedTabs = openTabs.map(openTab => ({
                    ...formatTabData(openTab),
                    tabId: openTab.id
                }));

                endTime = Date.now();
                console.log(`loadTabs formatTabData openTabs execution time1: ${endTime - startTime}ms`);

                set({
                    activeWindowId: currentWindow.id,
                    currentWindow,
                    activeTabId,
                    tabs: updatedTabs,
                    tabStoreInitialized: true
                });

                endTime = Date.now();
                console.log(`loadTabs set openTabs execution time1: ${endTime - startTime}ms`);

                get().loadTabsMetadata(updatedTabs);
                get().hotTabIndexes();

                endTime = Date.now();
                console.log(`loadTabs loadTabsMetadata execution time1: ${endTime - startTime}ms`);
            });
        });
    },

    loadTabsMetadata: async (updatedTabs) => {
        const openTabsWithMetadata = await Promise.all(
            updatedTabs.map(openTab => getTabMetadataCache(openTab.tabId).then(metadata => ({ tabId: openTab.tabId, metadata })))
        );
        const metadataMap = new Map(openTabsWithMetadata.map(result => [result.tabId, result.metadata]));
        const finalUpdatedTabs = updatedTabs.map(tab => {
            const tabMetadata = metadataMap.get(tab.tabId);
            return tabMetadata ? { ...tab, ...tabMetadata } : tab;
        });
        set({ tabs: finalUpdatedTabs });
    },

    sortByTabIds: (tabIds) => {
        set((state) => {
            const sortedTabs = tabIds.map((id, index) => {
                const tab = state.tabs.find(tab => tab.id === id);
                return tab ? { ...tab, index } : null;
            }).filter(Boolean);
            return { tabs: sortedTabs };
        });
    },
    hotTabIndexes: () => {
        const sortedTabs = sortBy(get().tabs, 'index').filter(tab => {
            const group = get().groups.find(group => group.id === tab.groupId);
            return !(tab.groupId && group && group.collapsed);
        }).slice(0, 8);

        const hotTabs = sortedTabs.map((tab, index) => ({
            tabId: tab.id,
            tabIndex: index + 1
        }));

        console.log("hotTabIndexes", hotTabs);

        set({ hotTabs });
    },

    getTabConfig: (tabId) => {
        if (!tabId) return;
        return get().tabs.find(tab => tab && tab.id === tabId);
    },

    setTabConfig: (tabId, newTabConfig) => {
        if (!tabId || !newTabConfig) {
            console.warn('setTabConfig: Invalid tabId or newTabConfig');
            return;
        }
        newTabConfig = { ...newTabConfig, tabId };
        set((state) => {
            const targetIndex = state.tabs.findIndex(tab => tab.id === tabId);
            if (targetIndex !== -1) {
                const updatedTabs = [...state.tabs];
                updatedTabs[targetIndex] = { ...updatedTabs[targetIndex], ...newTabConfig };
                return { tabs: updatedTabs };
            } else {
                return { tabs: [...state.tabs, newTabConfig] };
            }
        });
    },

    activeTab: (tabId) => {
        const currentTime = Date.now();
        set((state) => {
            const currentTab = state.tabs.find(tab => tab.id === tabId);
            if (!currentTab) return state;

            const updatedTabs = state.tabs.map(tab => {
                if (tab.windowId !== currentTab.windowId) return tab;
                const isActive = tab.id === tabId;
                return {
                    ...tab,
                    active: isActive,
                    selected: isActive,
                    highlighted: isActive,
                    lastAccessed: isActive ? currentTime : tab.lastAccessed
                };
            });
            return { tabs: updatedTabs, activeTabId: tabId };
        });
        setTabMetaCache(tabId, 'lastAccessed', currentTime);
    },

    setActiveWindowId: (windowId) => {
        set({ activeWindowId: windowId });
    },

    closeTab: async (tabId) => {
        set((state) => ({ tabs: state.tabs.filter(tab => tab.id !== tabId) }));
        get().hotTabIndexes();
        await clearTabMetadataCache(tabId);
    },

    resortTabs: debounce(async () => {
        const openTabs = await chrome.tabs.query({ windowType: 'normal' });
        for (const tab of openTabs) {
            const storedTab = get().tabs.find(storedTab => tab.id === storedTab.id);
            if (storedTab?.index !== tab.index) {
                get().setTabConfig(tab.id, { index: tab.index });
            }
        }
    }, 500),

    loadGroups: async () => {
        const startTime = Date.now();

        const openGroups = await chrome.tabGroups?.query({});
        set({ groups: openGroups });
        get().hotTabIndexes();
        let endTime = Date.now();
        console.log(`loadGroups windows  time: ${endTime - startTime}ms`);
    },

    setGroup: (groupId, groupConfig) => {
        if (!groupId) {
            console.warn('setGroup: groupId is null');
            return;
        }
        const newGroupConfig = { ...groupConfig, groupId };
        set((state) => {
            const targetIndex = state.groups.findIndex(group => group.id === groupId);
            if (targetIndex !== -1) {
                const updatedGroups = [...state.groups];
                updatedGroups[targetIndex] = { ...updatedGroups[targetIndex], ...newGroupConfig };
                return { groups: updatedGroups };
            } else {
                return { groups: [...state.groups, newGroupConfig] };
            }
        });
        get().hotTabIndexes();
    },

    initQuickAccess: async () => {
        const startTime = Date.now();

        const cacheInstance = await localCacheQuickAccessInstance();
        let quickAccess = await cacheInstance.get('quickAccess');
        const sortedQuickAccess = sortBy(quickAccess, 'sortIndex');
        set({ quickAccess: sortedQuickAccess });
        const endTime = Date.now();
        console.log(`initQuickAccess execution time: ${endTime - startTime}ms`);
    },

    setQuickAccess: async (uuid, config) => {
        if (config.urls && Array.isArray(config.urls)) {
            config = { ...pick(config, ['title', 'short_name', 'favIconUrl', 'color', 'type', 'urls', 'sortIndex']), uuid };
            config.urls = config.urls.map(urlConfig => ({
                ...pick(urlConfig, ['title', 'favIconUrl', 'url', 'sortIndex'])
            }));
        } else {
            config = { ...pick(config, ['title', 'favIconUrl', 'url', 'sortIndex']), uuid };
            if (!config.favIconUrl) {
                if (config.url.startsWith('http')) {
                    try {
                        config.favIconUrl = await fetchWebsiteIcon(config.url);
                    } catch (error) {
                        console.error(`fetchWebsiteIcon ${config.url} error`, error);
                        config.favIconUrl = faviconURL(config.url);
                    }
                } else {
                    config.favIconUrl = faviconURL(config.url);
                }
            }
        }

        const quickAccess = sortBy(get().quickAccess, 'sortIndex');
        const targetIndex = quickAccess.findIndex(q => q.uuid === uuid);

        if (config.urls) {
            if (targetIndex !== -1) {
                const updatedQuickAccess = [...quickAccess];
                updatedQuickAccess[targetIndex] = { ...updatedQuickAccess[targetIndex], ...config };
                set({ quickAccess: updatedQuickAccess });
            } else {
                const maxIndex = Math.max(...quickAccess.map(q => q.sortIndex), 0);
                set({ quickAccess: [...quickAccess, { ...config, sortIndex: maxIndex + 1 }] });
            }
        } else {
            const existingIndex = quickAccess.findIndex(q => q.title === config.title && q.favIconUrl === config.favIconUrl && q.url === config.url);

            if (existingIndex !== -1) {
                const updatedQuickAccess = [...quickAccess];
                updatedQuickAccess[existingIndex] = { ...updatedQuickAccess[existingIndex], ...config };
                set({ quickAccess: updatedQuickAccess });
            } else if (targetIndex !== -1) {
                const updatedQuickAccess = [...quickAccess];
                updatedQuickAccess[targetIndex] = { ...updatedQuickAccess[targetIndex], ...config };
                set({ quickAccess: updatedQuickAccess });
            } else {
                const maxIndex = Math.max(...quickAccess.map(q => q.sortIndex), 0);
                set({ quickAccess: [...quickAccess, { ...config, sortIndex: maxIndex + 1 }] });
            }
        }

        const cacheInstance = await localCacheQuickAccessInstance();
        await cacheInstance.set('quickAccess', get().quickAccess);
    },

    removeQuickAccess: async (uuid) => {
        const quickAccess = get().quickAccess;
        const newQuickAccess = quickAccess.filter(shortcut => shortcut.uuid !== uuid);
        set({ quickAccess: newQuickAccess });
        const cacheInstance = await localCacheQuickAccessInstance();
        await cacheInstance.set('quickAccess', newQuickAccess);
    },
}))), shallow);

const memoryCache = new Map();

export const initTabMetadataCache = async (tabId) => {
    const metadata = { lastAccessed: Date.now(), created_time: Date.now() };
    memoryCache.set(`tabid_${tabId}`, metadata);
    const cacheInstance = await localCacheMetadataInstance();
    await cacheInstance.set(`tabid_${tabId}`, metadata);
};

export const setTabMetaCache = async (tabId, key, value) => {
    try {
        const cacheInstance = await localCacheMetadataInstance();
        const existingMetadata = memoryCache.get(`tabid_${tabId}`) || await cacheInstance.get(`tabid_${tabId}`) || {};
        console.log("existingMetadata", tabId, existingMetadata);
        const updatedMetadata = { ...existingMetadata, [key]: value, tabId: tabId };
        memoryCache.set(`tabid_${tabId}`, updatedMetadata);
        await cacheInstance.set(`tabid_${tabId}`, updatedMetadata);
    } catch (error) {
        console.error(`Failed to set tab metadata cache for tabId ${tabId}:`, error);
    }
};

export const clearTabMetadataCache = async (tabId) => {
    memoryCache.delete(`tabid_${tabId}`);
    const cacheInstance = await localCacheMetadataInstance();
    await cacheInstance.remove(`tabid_${tabId}`);
};

export const getTabMetadataCacheKey = async (tabId, key) => {
    const cacheInstance = await localCacheMetadataInstance();
    const metadata = memoryCache.get(`tabid_${tabId}`) || await cacheInstance.get(`tabid_${tabId}`);
    return metadata ? metadata[key] : null;
};

export const getTabMetadataCache = async (tabId) => {
    const cacheInstance = await localCacheMetadataInstance();
    const metadata = memoryCache.get(`tabid_${tabId}`) || await cacheInstance.get(`tabid_${tabId}`) || {};
    const { lastAccessed = Date.now(), created_time = Date.now(), thumbnail, pipSupport, pipOn, pipMap, urlHistories, expandUrlHistory } = metadata;
    return { lastAccessed, created_time, thumbnail, pipSupport, pipOn, pipMap, urlHistories, expandUrlHistory };
};



let intersectionObserverInstance = null;
export const tabIntersectionObserver = () => {
    if (intersectionObserverInstance) return intersectionObserverInstance;
    intersectionObserverInstance = new IntersectionObserver((entries) => {
        for (const entry of entries) {
            tabStore.setState({ isActiveTabIntersecting: entry.isIntersecting });
        }
    }, { threshold: 0.5 });

    return intersectionObserverInstance;
};

export default tabStore;