const initGroupNames = () => {
    const groupKeys = [
        "group_newsInformation",
        "group_videoMovies",
        "group_liveStreaming",
        "group_entertainmentGames",
        "group_musicWebsites",
        "group_literatureNovels",
        "group_animeWebsites",
        "group_ecommerceShopping",
        "group_technologyDigital",
        "group_sportsFitness",
        "group_medicalHealth",
        "group_educationTraining",
        "group_skillTraining",
        "group_financialInvestment",
        "group_transportationTravel",
        "group_cookingGourmet",
        "group_petCare",
        "group_legalConsultation",
        "group_artCulture",
        "group_communityForums",
        "group_chatSocial",
        "group_commonQueries",
        "group_comprehensiveOthers",
        "group_jobRecruitment",
        "group_jobRecruitmentAlt",
        "group_motherBaby",
        "group_tutorialsGuides",
        "group_goodSitesCollection",
        "group_blogArticles",
        "group_onlineTools",
        "group_freeResources",
        "group_materialResources",
        "group_photography",
        "group_gamblingWebsites",
        "group_nsfw",
        "group_internalSystems"
    ];

    return groupKeys.map((name, index) => {
        return {
            id: index + 1,
            index: index,
            name: chrome.i18n.getMessage(name)
        }
    });
}
