import { Public } from "@mui/icons-material";
import { Avatar, CircularProgress, ListItemAvatar } from "@mui/material";
import React, { memo } from "react";

import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { faviconURL } from "../../../../utils/helpers";

const TabAvatar = ({ status, favIconUrl, url, pinned }) => {
    const { view, fontScale, PinnedTabsAsIcons } = useSettingsContext();
    const avatarSize = Math.floor(20 * fontScale);
    const loading = status === 'loading';
    const avatarUrl = favIconUrl || faviconURL(url);
    const pinnedAsIcon = pinned && PinnedTabsAsIcons;

    return (
        <ListItemAvatar sx={{ display: "flex", justifyContent: "center", minWidth: avatarSize }} >
            <Avatar
                variant="square"
                slotProps={{ img: { referrerPolicy: "no-referrer" } }}
                sx={{
                    background: "transparent",
                    width: avatarSize,
                    height: avatarSize,
                    alignContent: "center",
                }}
                aria-label="recipe"
                src={avatarUrl}>
                <Public sx={{ fontSize: avatarSize }} color="primary" />
            </Avatar>
            {loading && (
                <CircularProgress
                    thickness={2.5}
                    sx={{ position: "absolute", "&.MuiCircularProgress-colorPrimary": { color: "rgba(0,0,0,0.25)" } }}
                    size={avatarSize + 1}
                />
            )}
        </ListItemAvatar>
    );
};

export default memo(TabAvatar);