import React, { createContext, useContext, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import i18n from '../../utils/i18n';
import userStore from '../../core/stores/userStore';
import { sendRuntimeMessage } from '../../core/browers/runtime';

const TranslationContext = createContext();

export const TranslationProvider = ({ children, defaultNS = 'translation' }) => {
    const { t } = useTranslation(defaultNS);

    const { updateSetting, language } = userStore((state) => ({
        updateSetting: state.updateSetting,
        language: state.settings.selectedLanguage,
    }));

    useEffect(() => {
        i18n.changeLanguage(language);
    }, [language]);

    const changeLanguage = async (lng) => {
        try {
            await i18n.changeLanguage(lng);
            await updateSetting("selectedLanguage", lng);
            await sendRuntimeMessage("update_setting", { key: "selectedLanguage", value: lng });
            const isRtl = ['ar', 'fa', 'he', 'ur', 'ku'].includes(lng);
            await updateSetting("rtl", isRtl);
            await sendRuntimeMessage("update_setting", { key: "rtl", value: isRtl });
        } catch (error) {
            console.error('Failed to change language:', error);
        }
    };

    const contextValue = {
        t,
        language,
        changeLanguage,
        defaultNS
    };

    return (
        <TranslationContext.Provider value={contextValue}>
            {children}
        </TranslationContext.Provider>
    );
};

export const useTranslationContext = (ns, options) => {
    const context = useContext(TranslationContext);
    if (!context) {
        throw new Error('useTranslationContext must be used within a TranslationProvider');
    }
    let i18nT;
    if (typeof ns === 'undefined') {
        i18nT = context.t;
    } else if (typeof ns === 'object') {
        i18nT = useTranslation(context.defaultNS, ns).t;
    } else {
        i18nT = useTranslation(ns, options).t;
    }
    return { ...context, t: i18nT };
};