import React, { useState, useRef, memo, useEffect, useCallback, useMemo } from "react";
import { ListItem, ListItemButton, Box, Stack, IconButton } from "@mui/material";
import { Close } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { getTabItemStyle } from "../Hooks/useTabItemStyle";
import TabAvatar from "../SidePanel/tabs/tab/TabAvatar";
import TabTitle from "../SidePanel/tabs/tab/TabTitle";
import TabPip from "../SidePanel/tabs/tab/TabPip";
import TabVolume from "../SidePanel/tabs/tab/TabVolume";
import TabUnPin from "../SidePanel/tabs/tab/TabUnPin";
import TabFocus from "../SidePanel/tabs/tab/TabFocus";
import TabSelected from "../SidePanel/tabs/tab/TabSelected";
import { useSettingsContext } from "../Providers/SettingsProvider";
import tabStore from "../../core/stores/tabStore";

export default memo(function TabItem({ tabId, index, keyboardSelected }) {
  const {
    view,
    AlwaysShowCloseButton,
    buttonPosition,
  } = useSettingsContext();

  const tabRef = useRef(null);
  const tabData = tabStore(useCallback((state) => {
    const tab = state.tabs.find((t) => t.id === tabId) || {};
    return {
      isTabActive: tab?.active,
      tabWindowId: tab?.windowId,
      title: tab.title,
      url: tab.url,
      lastAccessed: tab.lastAccessed,
      status: tab.status,
      favIconUrl: tab.favIconUrl,
      pinned: tab.pinned,
      discarded: tab.discarded,
      pipOn: tab.pipOn,
      audible: tab.audible,
      mutedInfo: tab.mutedInfo,
      hostname: tab.hostname,
      currentWindowId: state.currentWindow?.id,
    };
  }, [tabId]));

  const isActiveTab = useMemo(() =>
    tabData.isTabActive && tabData.currentWindowId === tabData.tabWindowId,
    [tabData.isTabActive, tabData.currentWindowId, tabData.tabWindowId]
  );

  const handleMiddleMouseDown = useCallback((event) => {
    if (event.button === 1) {
      sendRuntimeMessage('bulk_close_tabs', { tabIds: [tabId] });
    }
  }, [tabId]);

  const handleCloseTab = useCallback((event) => {
    event.stopPropagation();
    sendRuntimeMessage('bulk_close_tabs', { tabIds: [tabId] });
  }, [tabId]);

  const handleHighlightTab = useCallback((event) => {
    event.stopPropagation();
    sendRuntimeMessage('highlight_tab', { tabId });
  }, [tabId]);

  const handleKeyDown = useCallback((event) => {
    event.stopPropagation();
    if (keyboardSelected && event.key === 'Enter') {
      handleHighlightTab(event);
    }
  }, [keyboardSelected, handleHighlightTab]);

  useEffect(() => {
    if (keyboardSelected) {
      tabRef.current?.focus();
    }
  }, [keyboardSelected]);

  if (!tabData.title) {
    return null;
  }

  const tabItemStyle = useMemo(() =>
    getTabItemStyle(tabId, tabData.discarded),
    [tabId, tabData.discarded]
  );

  return (
    <Box
      data-tab-index={index}
      className={`tab-item${isActiveTab ? " active" : ""}${keyboardSelected ? " keyboard-selected" : ""}`}
      sx={{ position: "relative" }}
      onKeyDown={handleKeyDown}
      tabIndex={0}
    >
      <ListItem
        ref={tabRef}
        disablePadding
        onClick={handleHighlightTab}
        onMouseDown={handleMiddleMouseDown}
      >
        <ListItemButton
          title={tabData.title}
          component="div"
          dense={view === "list"}
          disableGutters
          selected={isActiveTab || keyboardSelected}
          sx={tabItemStyle}
        >
          <Stack
            useFlexGap
            sx={{
              width: "100%",
              paddingX: "10px",
            }}
            direction={buttonPosition}
            alignItems="center"
            justifyContent="space-between"
            spacing={0.5}
          >
            <Box sx={{ padding: buttonPosition === "row" ? "0 5px 0 10px" : "0 10px 0 5px" }}>
              <TabAvatar
                status={tabData.status}
                favIconUrl={tabData.favIconUrl}
                url={tabData.url}
                pinned={tabData.pinned}
              />
            </Box>
            <TabTitle title={tabData.title} url={tabData.url} hostname={tabData.hostname} lastAccessed={tabData.lastAccessed} />
            {tabData.pipOn && <TabPip tabId={tabId} />}
            {tabData.audible && <TabVolume tabId={tabId} muted={tabData.mutedInfo.muted} />}
            {tabData.pinned && <TabUnPin tabId={tabId} />}
            {isActiveTab && <TabFocus />}
            {keyboardSelected ? <TabSelected /> : (
              <IconButton
                className="close-button"
                sx={{
                  padding: "4px",
                  color: "inherit",
                  display: AlwaysShowCloseButton ? "flex" : "none"
                }}
                onClick={handleCloseTab}
              >
                <Close size="small" />
              </IconButton>
            )}
          </Stack>
        </ListItemButton>
      </ListItem>
    </Box>
  );
});