import { isValidUrl } from "../../utils/helpers";
import { delay } from "lodash-es";
import chromeStorage from "../../utils/chromeStorage";
import { getTabMetadataCacheKey, setTabMetaCache, tabStore } from "../stores/tabStore";
import { userStore } from "../stores/userStore";
import { availableColors } from "../../utils/colors";
import { getUserConfig } from "../user";
import { siteStore } from "../stores/siteStore";
import { sendTabMessage } from "./runtime";

export async function getActiveTab() {
    let tabs = await chrome.tabs.query({ active: true, url: ["http://*/*", "https://*/*"] });
    const activeTab = tabs[0];
    return activeTab;
}

export const getActiveTabUrl = async () => {
    const tab = await getActiveTab()

    return tab?.url
}

export const setTabActive = async (tabId, tab) => {
    try {
        await chromeStorage('session').set('setLockActiveTabId', tabId)

        if (!tab) {
            tab = await chrome.tabs.get(tabId);
        }
        const tabWindow = await chrome.windows.get(tab.windowId);
        if (tabWindow.focused === false) {
            await chrome.windows.update(tab.windowId, { drawAttention: true, focused: true });
            tabStore.setState({ activeWindowId: tab.windowId })
        }
        if (tab.discarded) {
            if (tab.active) {
                await chrome.tabs.reload(tabId)
            } else {
                await chrome.tabs.update(tab.id, { active: true });
            }
            return
        }
        if (tab.active && tab.highlighted) {
            tabStore.getState().activeTab(tab.id);
            return true;
        }
        await chrome.tabs.highlight({ tabs: tab.index, windowId: tab.windowId });

        if (tab.groupId > -1) {
            //if tab has group,check if group is collapsed
            const groupInfo = await chrome.tabGroups?.get(tab.groupId);
            if (groupInfo.collapsed) {
                await chrome.tabGroups?.update(tab.groupId, { collapsed: false });
            }
        }
        return true
    } catch (error) {
        console.error("setTabActive error", error)
        throw error
    } finally {
        setTimeout(() => {
            chromeStorage('session').remove('setLockActiveTabId', tabId)
        }, 200);
    }

}

export async function getCurrentTab() {
    let queryOptions = { active: true, lastFocusedWindow: true };
    // `tab` will either be a `tabs.Tab` instance or `undefined`.
    let [tab] = await chrome.tabs.query(queryOptions);
    return tab;
}

export const removeTabById = async (tabId) => {

    await addTabRemoveList(tabId);
    try {
        await chrome.tabs.remove(tabId)
        tabStore.getState().closeTab();
    } catch (error) {
        console.error('removeTabById error', { tabId, error: error.message })
    } finally {
        delay(reduceTabRemoveList, 5000, tabId)
    }
}

const tabRemoveListCache = new Set();

export const addTabRemoveList = async (tabId) => {
    tabRemoveListCache.add(tabId);
    const cache = (await chromeStorage('session').get('tabRemoveList')) || [];
    const newCacheIds = [...cache, tabId];
    await chromeStorage('session').set('tabRemoveList', newCacheIds);
}

export const isTabInRemoveList = async (tabId) => {
    if (tabRemoveListCache.has(tabId)) {
        return true;
    }
    const removeList = (await chromeStorage('session').get('tabRemoveList')) || [];
    removeList.forEach(id => tabRemoveListCache.add(id));
    return tabRemoveListCache.has(tabId);
}

export const reduceTabRemoveList = async (tabId) => {
    tabRemoveListCache.delete(tabId);
    const cache = (await chromeStorage('session').get('tabRemoveList')) || [];
    await chromeStorage('session').set('tabRemoveList', cache.filter(id => id !== tabId));
}

export const formatTabData = (tab) => {
    let tabConfig = { ...tab };
    tabConfig.url = tab.pendingUrl || tab.url;

    const parseUrl = (url) => {
        try {
            const parsedUrl = new URL(url);
            return { hostname: parsedUrl.hostname, origin: parsedUrl.origin };
        } catch (e) {
            console.error(`parseUrl ${url} error`, e);
            return { hostname: url, origin: url };
        }
    };

    const { hostname, origin } = parseUrl(tabConfig.url);
    tabConfig.hostname = hostname;
    tabConfig.origin = origin;
    tabConfig.tabId = tab.id;
    tabConfig.color = availableColors[tab.id % availableColors.length];

    return tabConfig;
}

export const sendMessageToAllTabs = async (action, data = {}) => {
    const queryOptions = { windowType: "normal", url: ["http://*/*", "https://*/*"] };
    if (data.windowId !== undefined) {
        queryOptions.windowId = data.windowId;
    }
    const tabs = await chrome.tabs.query(queryOptions);

    const sendMessages = tabs.map(tab => sendTabMessage(tab.id, action, data));
    await Promise.all(sendMessages);
}

export const handleThumbnailCreation = async (tabId) => {
    try {

        const { tabPreviewEnable, tabPreviewImage } = userStore.getState().settings;
        if (!tabPreviewEnable || !tabPreviewImage) {
            return null
        }

        const checkTab = await chrome.tabs.get(tabId);
        if (!checkTab.active) {
            console.warn("tab not active yet", checkTab);
            return null;
        }
        if (checkTab.status !== "complete" && checkTab.active && checkTab.discarded === false) {
            console.warn("tab not ready yet", checkTab);
            return null;
        }
        if (!isValidUrl(checkTab.url)) {
            console.warn("tab url invalid", checkTab);

            return null;
        }
        const tabWwindow = await chrome.windows.get(checkTab.windowId, { populate: true });
        if (!tabWwindow.focused) {
            console.warn("widnow not focused", checkTab, tabWwindow);
            return null
        }
        const activeTab = tabWwindow.tabs.find(tab => tab.active);
        if (activeTab.id !== checkTab.id) {

            return null
        }

        const thumbnail = await chrome.tabs.captureVisibleTab(checkTab.windowId, { format: "jpeg", quality: 50 });
        return thumbnail;
    } catch (error) {
        console.error("handleThumbnailCreation operation failed:", error);
        return null;
    }
}

export const playingVideoSwitchPip = async (tabId, type, action) => {

    const settings = await getUserConfig('user_settings');
    if (type === 'tab' && !settings.tabSwitchPip) {
        return false;
    }
    if (type === 'scroll' && !settings.scrollSwitchPip) {
        return false;
    }
    const tabPipSupport = await getTabMetadataCacheKey(tabId, 'pipSupport')
    if (!tabPipSupport) {
        return false;
    }
    try {
        const tab = await chrome.tabs.get(tabId);
        const domainConfig = await siteStore.getState().getSiteConfig(new URL(tab.url).hostname);
        if (domainConfig?.tabSwitchPip === false) {
            return false;
        }
        await sendTabMessage(tabId, action);

    } catch (error) {
        console.error('playing_video_switch_pip error', error)
    }

}

export const saveTabPictureInPictureInfo = async (tabId, frameId, data) => {

    let pipMap = await getTabMetadataCacheKey(tabId, 'pipMap')
    if (!pipMap && frameId === 0) {
        pipMap = {};
    }
    await setTabMetaCache(tabId, 'pipMap', { ...pipMap, [frameId]: data })
    const pipSupport = await checkPipInfo(tabId, 'pipSupport')
    await setTabMetaCache(tabId, 'pipSupport', pipSupport)
    const pipOn = await checkPipInfo(tabId, 'pipOn')
    await setTabMetaCache(tabId, 'pipOn', pipOn)

    return { pipMap, pipSupport, pipOn }
}

const checkPipInfo = async (tabId, key) => {
    let pipMap = await getTabMetadataCacheKey(tabId, 'pipMap')
    if (!pipMap) {
        return false
    }
    for (const frameId in pipMap) {
        if (pipMap[frameId][key]) {
            return true;
        }
    }
    return false;
}
