import humanizeDuration from "humanize-duration";
import React, { useState, useEffect, memo } from "react";
import { Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { siteStore } from "../../../../core/stores/siteStore";

const TabTimeAgo = ({ hostname, lastAccessed }) => {

  const { t, language } = useTranslationContext();

  const { theme, fontColor } = useSettingsContext();

  const tabSiteConfig = siteStore(state => (state.sites.find((d) => d.hostname === hostname)))


  const [now, setNow] = useState(Date.now());

  useEffect(() => {
    const timerId = setInterval(() => setNow(Date.now()), 1000);
    return () => clearInterval(timerId);
  }, []);

  const humanizeOptions = {
    delimiter: " ",
    largest: 1,
    round: true,
    language: language.replace("-", "_"),
    fallbacks: ["en"]
  };

  const humanTimeStr = humanizeDuration(now - lastAccessed, humanizeOptions);
  const timeAgo = `${hostname} • ${humanTimeStr} ${t("ago")}`;

  return (
    <Typography
      variant="h5"
      noWrap
      sx={{
        fontWeight: 400,
        color: tabSiteConfig?.fontColor ?? (theme === "dark" ? "text.primary" : fontColor || "text.primary"),
        opacity: 0.6
      }}
    >
      {timeAgo}
    </Typography>
  );
};

export default memo(TabTimeAgo);
