import React, { useEffect, useState, useRef } from "react";
import { Button, Divider, Grid, IconButton, List, Stack, TextField } from "@mui/material";
import TabItem from "./TabItem";
import { filterTabs } from "../../utils/helpers";
import { useSettingsContext } from "../Providers/SettingsProvider";
import tabStore from "../../core/stores/tabStore";
import { Search } from "@mui/icons-material";
import { Clear } from "@mui/icons-material";
import { Add } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import BgOverlay from "../SidePanel/BgOverlay";
import userStore from "../../core/stores/userStore";

export default function TabList() {

  const [state, setState] = useState({
    focused: false,
    localSearchText: "",
    selectedTabIndex: -1,
    keyboardEnabled: false
  });


  const { fontColor } = userStore((state) => ({
    fontColor: state.settings.fontColor,
  }));

  const searchInputRef = useRef(null);

  const { currentWindow } = tabStore((state) => ({
    currentWindow: state.currentWindow,
  }));
  const { tabStoreInitialized, initTabs } = tabStore((state) => ({
    tabStoreInitialized: state.tabStoreInitialized,
    initTabs: state.initTabs,
  }));

  useEffect(() => {
    initTabs();
  }, [initTabs]);

  useEffect(() => {
    if (searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, []);

  useEffect(() => {
    if (state.selectedTabIndex >= 0) {
      const selectedElement = document.querySelector(`[data-tab-index="${state.selectedTabIndex}"]`);
      if (selectedElement) {
        selectedElement.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
      }
    }
  }, [state.selectedTabIndex]);

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.data.type === 'SIDEBAR_LOADED') {
        if (searchInputRef.current) {
          searchInputRef.current.focus();
        }
      }
      if (event.data.type === 'SIDEBAR_SHOWN') {
        if (searchInputRef.current) {
          searchInputRef.current.focus();
        }
      }
      if (event.data.type === 'SIDEBAR_HIDDEN') {
        setState({ focused: false, localSearchText: "", selectedTabIndex: -1 });
      }
    };

    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, []);

  const tabs = tabStore(state => state.tabs
    .filter(tab => tab.windowId === currentWindow.id)
    .map(tab => {
      return {
        id: tab.id,
        title: tab.title,
        url: tab.url,
        hostname: tab.hostname,
        created_time: tab.created_time,
        lastAccessed: tab.lastAccessed,
        index: tab.index,
        windowId: tab.windowId,
        groupId: tab.groupId,
        pinned: tab.pinned
      }
    })
  ) || [];

  const filteredTabs = filterTabs(tabs, state.localSearchText, 'asc', 'index', currentWindow.id);

  const handleSearchInput = (event) => {
    const value = event.target.value;
    setState((prevState) => ({ ...prevState, localSearchText: value, selectedTabIndex: -1 }));
  };

  const handleClearClick = () => {
    setState({ focused: false, localSearchText: "", selectedTabIndex: -1 });
  };

  const handleTabAddNew = () => {
    sendRuntimeMessage('add_new_tab');
  }

  const handleKeyDown = (event) => {
    event.stopPropagation();
    if (state.keyboardEnabled) {
      if (event.key === 'ArrowDown') {
        setState((prevState) => ({
          ...prevState,
          selectedTabIndex: (prevState.selectedTabIndex + 1) % filteredTabs.length
        }));
      } else if (event.key === 'ArrowUp') {
        setState((prevState) => ({
          ...prevState,
          selectedTabIndex: (prevState.selectedTabIndex - 1 + filteredTabs.length) % filteredTabs.length
        }));
      } else if (event.key === 'Enter' && state.selectedTabIndex >= 0) {
        const selectedTab = filteredTabs[state.selectedTabIndex];
        sendRuntimeMessage('highlight_tab', { tabId: selectedTab.id });
        setState((prevState) => ({ ...prevState, selectedTabIndex: -1 }));
      } else if (event.key === 'Escape') {
        setState((prevState) => ({ ...prevState, selectedTabIndex: -1 }));
      }
    }
  };

  const handleMouseEnter = () => {
    setState((prevState) => ({ ...prevState, keyboardEnabled: true }));
  };

  const handleMouseLeave = () => {
    setState((prevState) => ({ ...prevState, keyboardEnabled: false }));
  };

  return (
    <Stack sx={{ height: "100vh", backdropFilter: "blur(15px)" }} onKeyDown={handleKeyDown} tabIndex={0} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      <BgOverlay />
      <Grid container justifyContent="center" alignItems={"center"} p={1} sx={{ position: 'sticky', top: 0, zIndex: 1 }}>
        <TextField
          id="search-text-input"
          size="small"
          fullWidth
          placeholder="Search"
          variant="outlined"
          autoComplete="off"
          focused={state.focused}
          value={state.localSearchText}
          inputRef={searchInputRef}
          InputProps={{
            startAdornment: <Search sx={{ mr: 1, fontSize: "1rem", color: fontColor || 'text.primary', }} />,
            endAdornment: state.localSearchText.length > 0 && (
              <React.Fragment>
                <IconButton onClick={handleClearClick}>
                  <Clear size="small" sx={{ color: fontColor || 'text.primary', }} />
                </IconButton>
              </React.Fragment>
            ),
            sx: {
              borderColor: fontColor || 'text.primary',
              color: fontColor || 'text.primary',
              '&::placeholder': {
                color: fontColor || 'text.primary',
                opacity: 1
              },
            },
          }}
          onChange={handleSearchInput}
          onFocus={() => setState((prevState) => ({ ...prevState, focused: true }))}
          onMouseEnter={() => setState((prevState) => ({ ...prevState, focused: true }))}
          onMouseLeave={() => setState((prevState) => ({ ...prevState, focused: false }))}
        />
      </Grid>

      {/* <Grid container sx={{ flex: 1, overflowY: 'auto' }}> */}
      <OverlayScrollbarsComponent>
        {/* <List sx={{ width: "100%", padding: 0 }}> */}
        {filteredTabs.map((tab, index) => {
          return <TabItem key={`tab-${tab.id}`} index={index} tabId={tab.id} keyboardSelected={index === state.selectedTabIndex} />;
        })}
        {/* </List> */}
      </OverlayScrollbarsComponent>
      {/* </Grid> */}
      <Divider sx={{ my: 1 }} />
      <Button sx={{ padding: "5px", position: 'sticky', bottom: 0, zIndex: 1, color: fontColor || 'text.primary' }} onClick={handleTabAddNew}>
        <Add />
      </Button>
    </Stack>
  );
}
