import { immer } from "zustand/middleware/immer";
import { userPublicLocalStorage } from "../user";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';
/*eslint no-return-await: "error"*/
export const localCacheSiteInstance = async () => {
    return userPublicLocalStorage('sites')
};

export const siteStore = createWithEqualityFn(subscribeWithSelector(immer((set, get) => ({
    sites: [],
    searchText: "",
    loadSiteConfigs: async () => {
        const cacheInstance = await localCacheSiteInstance();
        const savedSites = await cacheInstance.all();
        set({ sites: savedSites });
    },
    getSiteConfig: async (hostname) => {
        if (!hostname) return;
        if (!get().sites.length) {
            await get().loadSiteConfigs();
        }
        const site = get().sites.find(site => site.hostname === hostname);
        if (site) {
            return site;
        } else {
            const cacheInstance = await localCacheSiteInstance();
            return cacheInstance.get(`site_config_${hostname}`);
        }
    },
    setSiteConfig: async (hostname, config) => {
        if (!hostname) {
            console.warn('setSiteConfig hostname is null');
            return;
        }
        if (!config) {
            console.warn('setSiteConfig config is null');
            return;
        }
        const newConfig = { ...config, hostname };
        set((state) => {
            const targetIndex = state.sites.findIndex(d => d.hostname === hostname);
            if (targetIndex !== -1) {
                const updatedSites = [...state.sites];
                updatedSites[targetIndex] = { ...updatedSites[targetIndex], ...newConfig };
                return { sites: updatedSites };
            } else {
                return { sites: [...state.sites, newConfig] };
            }
        })
        const cacheInstance = await localCacheSiteInstance();
        const cache = await cacheInstance.get(`site_config_${hostname}`);
        await cacheInstance.set(`site_config_${hostname}`, { ...cache, ...newConfig });

    },
    deleteSiteConfig: async (hostname) => {
        set({ sites: get().sites.filter(d => d.hostname !== hostname) });
        const cacheInstance = await localCacheSiteInstance();
        await cacheInstance.remove(`site_config_${hostname}`);
    }

}
)
)), shallow)
