import { PushPin } from "@mui/icons-material";
import { IconButton, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


const TabUnPin = memo(({ tabId }) => {
  const { t } = useTranslationContext();

  const handleUnPinTab = async (event) => {
    event.preventDefault();
    event.stopPropagation();
    await chrome.tabs.update(tabId, { pinned: false });
  };


  return (
    <Tooltip arrow placement="top" title={<Typography>{t('tabMenu.UnpinTab')}</Typography>}>
      <IconButton sx={{ padding: "4px", color: "inherit" }} onClick={handleUnPinTab}>
        <PushPin size="small" />
      </IconButton>
    </Tooltip>
  );
});

export default TabUnPin;
