import { Box, CircularProgress, Typography } from "@mui/material";
import React, { memo } from "react";

const PageLoading = () => {
    return (
        <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", margin: "50px auto" }}>
            <CircularProgress size={35} color="secondary" thickness={2} />
        </Box>
    );
};

export default memo(PageLoading);