import { get as _get } from "lodash-es";

const chromeStorageInstances = new Map();

class chromeStorage {
    constructor(area = "session") {
        this.store = null;
        this.stores = [];
        this.initStore(area);
    }

    initStore(area) {
        if (this.stores[area]) {
            this.store = this.stores[area];
        } else {
            this.store = chrome.storage[area];
        }
    }

    async set(key, items) {
        this.checkStore();
        await this.store.set({ [key]: items });
        return items;
    }

    async get(key) {
        this.checkStore();
        let cacheItem = await this.store.get([key]);
        return _get(cacheItem, key);
    }

    async getOrSet(key, items) {
        this.checkStore();
        let cacheItem = await this.get(key);
        if (cacheItem) return cacheItem;
        return await this.set(key, items);
    }

    async remove(key) {
        this.checkStore();
        return await this.store.remove(key);
    }

    async delete(key) {
        this.checkStore();
        return await this.store.remove(key);
    }

    async clear() {
        this.checkStore();
        return await this.store.clear();
    }

    async prune() {
        this.checkStore();
        return await this.store.clear();
    }

    async keys(match = "") {
        this.checkStore();
        const keys = Object.keys(await this.store.get());
        if (!match) return keys;
        return Array.from(keys).filter((key) => key.indexOf(match) > -1);
    }

    async whereIn(keys) {
        this.checkStore();
        const results = [];
        try {
            const promises = keys.map(key => this.get(key));
            const values = await Promise.all(promises);
            for (const value of values) {
                if (value) results.push(value);
            }
        } catch (error) {
            console.error(error);
        }
        return results;
    }

    checkStore() {
        if (!this.store) {
            this.initStore();
        }
    }
}

export default (area = "session") => {
    if (chromeStorageInstances.has(area)) {
        return chromeStorageInstances.get(area);
    }
    const store = new chromeStorage(area);
    chromeStorageInstances.set(area, store);
    return store;
};
