import { PictureInPictureAlt } from "@mui/icons-material";
import { IconButton, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { sendTabMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


const TabPip = ({ tabId }) => {
  const { t } = useTranslationContext();

  const handleClosePip = (event) => {
    event.preventDefault();
    event.stopPropagation();
    sendTabMessage(tabId, 'exitPictureInPicture');
  };


  return (
    <Tooltip arrow placement="top" title={<Typography>{t('tabMenu.ExitPip')}</Typography>}>
      <IconButton sx={{ padding: "4px", color: "inherit" }} onClick={handleClosePip}>
        <PictureInPictureAlt sx={{ fontSize: "1rem", color: "inherit" }} />
      </IconButton>
    </Tooltip>
  );
};

export default memo(TabPip);
