
const defaultFontFamily = `Inter var,ui-sans-serif,system-ui,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji`;

export const themeOptions = (mode, fontScale = 1, fontFamily, disableAnimations = true) => {
    fontFamily = fontFamily || defaultFontFamily
    const fontSizeMultiplier = fontScale >= 1.5 ? 1.5 : fontScale <= 0.5 ? 0.5 : fontScale;

    return {
        palette: {
            mode: mode,
            primary: {
                main: '#334155',
            },
            secondary: {
                main: '#6c7a91',
            },
            error: {
                main: '#e11d48',
            },
            warning: {
                main: '#ea580c',
            },
            info: {
                main: '#0891b2',
            },
            success: {
                main: '#059669',
            },
        },
        typography: {
            fontSize: fontSizeMultiplier * 16,
            fontFamily: fontFamily,
            h1: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1.75}rem`,
                lineHeight: `${fontSizeMultiplier * 2.25}rem`,
                fontWeight: 600,
            },
            h2: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1.5}rem`,
                lineHeight: `${fontSizeMultiplier * 2}rem`,
                fontWeight: 600,
            },
            h3: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1.25}rem`,
                lineHeight: `${fontSizeMultiplier * 1.75}rem`,
                fontWeight: 600,
            },
            h4: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1}rem`,
                // lineHeight: `${fontSizeMultiplier * 1.5}rem`,
                fontWeight: 600,
            },
            h5: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 0.875}rem`,
                lineHeight: `${fontSizeMultiplier * 1.25}rem`,
                fontWeight: 400,
            },
            h6: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 0.75}rem`,
                lineHeight: `${fontSizeMultiplier * 1}rem`,
                fontWeight: 400,
            },
            subtitle1: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1}rem`,
                lineHeight: `${fontSizeMultiplier * 1.5}rem`,
            },
            subtitle2: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 0.875}rem`,
                lineHeight: `${fontSizeMultiplier * 1.25}rem`,
            },
            body1: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 1}rem`,
                lineHeight: `${fontSizeMultiplier * 1.5}rem`,
                fontWeight: 400,
            },
            body2: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 0.875}rem`,
                lineHeight: `${fontSizeMultiplier * 1.25}rem`,
            },
            button: {
                fontSize: `${fontSizeMultiplier * 1}rem`,
                lineHeight: `${fontSizeMultiplier * 1.5}rem`,
                fontWeight: 400,
                textTransform: 'none'
            },
            caption: {
                fontFamily: fontFamily,
                fontSize: `${fontSizeMultiplier * 0.75}rem`,
                lineHeight: `${fontSizeMultiplier * 1.25}rem`,
            },
        },
        props: {
            MuiButton: {
                size: 'small',
            },
            MuiButtonGroup: {
                size: 'small',
            },
            MuiCheckbox: {
                size: 'small',
            },
            MuiFab: {
                size: 'small',
            },
            MuiFormControl: {
                margin: 'dense',
                size: 'small',
            },
            MuiFormHelperText: {
                margin: 'dense',
            },
            MuiIconButton: {
                size: 'small',
            },
            MuiInputBase: {
                margin: 'dense',
            },
            MuiInputLabel: {
                margin: 'dense',
            },
            MuiRadio: {
                size: 'small',
            },
            MuiSwitch: {
                size: 'small',
            },
            MuiTextField: {
                margin: 'dense',
                size: 'small',
            },
            MuiList: {
                dense: true,
            },
            MuiMenuItem: {
                dense: true,
            },
            MuiTable: {
                size: 'small',
            },
            MuiTooltip: {
                arrow: true,
            },
        },
        shape: {
            borderRadius: 4,
        },
        components: {
            MuiButtonBase: {
                defaultProps: {
                    disableRipple: disableAnimations ? true : false,
                },
                styleOverrides: {
                    root: {
                        fontSize: `${fontSizeMultiplier * 1.5}rem`,
                    }
                }

            },
            MuiBottomNavigationAction: {
                styleOverrides: {
                    root: {
                        fontSize: `${fontSizeMultiplier * 1}rem`,
                    }
                }
            },
            MuiSvgIcon: {
                styleOverrides: {
                    root: {
                        color: mode === "dark" ? "#fff" : "text.primary",
                        fontSize: `${fontSizeMultiplier * 1.25}rem`,
                        fontWeight: 100
                    }
                }
            },
            MuiIconButton: {
                styleOverrides: {
                    root: {
                        Padding: "5px",
                    }
                }
            }
        },

        ...(disableAnimations ? {
            transitions: {
                create: () => 'none',
            },
            MuiCssBaseline: {
                styleOverrides: {
                    '*, *::before, *::after': {
                        transition: 'none !important',
                        animation: 'none !important',
                    },
                },
            },
        } : {})


    }
}


