export const getBackgroundByColor = (color, listBackgroundStyle, deg = 90, alpha = 0) => {


    let background = 'transparent';
    deg = deg || calculateClockAngle();
    switch (listBackgroundStyle) {
        case 'single':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)}, ${hexToRgb(color.hex, alpha / 2)})
            `
            break;
        case 'none':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(noColor.hex, alpha)}, ${hexToRgb(noColor.hex, alpha / 10)})
            `
            break;
        case 'simple':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)}, ${hexToRgb(color.hex, alpha / 10)})
            `
            break;
        case 'complementary':
            background = `
                linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)}, ${hexToRgb(color.complementary, alpha)})
                `
            break;
        case 'analogous':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)}, ${hexToRgb(color.analogous[0], alpha)}, ${hexToRgb(color.analogous[1], alpha)})`
            break;
        case 'triadic':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)}, ${hexToRgb(color.triadic[0], alpha)}, ${hexToRgb(color.triadic[1], alpha)})`
            break;
        case 'tetradic':
            background = `
            linear-gradient(${deg}deg,${hexToRgb(color.hex, alpha)},  ${hexToRgb(color.tetradic[0], alpha)},${hexToRgb(color.tetradic[1], alpha)}, ${hexToRgb(color.tetradic[2], alpha)})`
            break;
    }
    return background;
}


export const availableColors = customColors.filter(color => color.color !== color.tabGroupColor);
export const chromeGroupAvailableColors = customColors.filter(color => color.color === color.tabGroupColor);
let colorIndex = 0;
export const getCustomColor = (colorName, index) => {
    let color = noColor;
    if (colorName === false) {
        return color;
    }
    const chromeGroupColor = customColors.find(color => color.color === colorName);
    if (chromeGroupColor) {
        color = chromeGroupColor;
        return color;
    }
    color = availableColors[(index ? index : colorIndex) % availableColors.length];
    colorIndex++;
    return color;
}

export const sampleGroupColor = () => {
    return sample(customColors)
}


export const complementaryColorCalculator = (color, hex) => {
    if (hex) {
        return '#' +
            ("0" + (255 - parseInt(color.substring(1, 3), 16)).toString(16)).slice(-2) +
            ("0" + (255 - parseInt(color.substring(3, 5), 16)).toString(16)).slice(-2) +
            ("0" + (255 - parseInt(color.substring(5, 7), 16)).toString(16)).slice(-2);
    } else {
        return [(255 - color[0]), (255 - color[1]), (255 - color[2])];
    }
}